body, body, h3, p {
  margin: 0;
}

h3 {
  font-size: 1em;
}

.card {
  color: #616070;
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  width: 200px;
  height: 408px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  transition-duration: .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__product-image {
  height: 134px;
  margin: 32px 19px 0;
  display: block;
}

.card__product-name {
  color: #060b35;
  margin: 40px 16px 0;
  font-weight: 500;
  transition-duration: .3s;
}

.card:hover .card__product-name {
  color: #34568b;
}

.card__product-code {
  color: #616070;
  margin: 4px 84px 0 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__product-rating {
  justify-content: space-between;
  align-items: center;
  margin: 16px 16px 0;
  display: flex;
}

.card__product-reviews {
  text-align: right;
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
  text-decoration: none;
}

.card__product-info {
  justify-content: space-between;
  margin: 24px 16px 0;
  display: flex;
}

.card__product-info-price {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  box-sizing: border-box;
  text-transform: uppercase;
  margin-inline: auto;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition-duration: .3s;
  display: flex;
}

.card__button:hover {
  cursor: pointer;
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin: 0;
}

.stars--0 > :nth-child(0), .stars--1 > :first-child, .stars--2 > :nth-child(-n+2), .stars--3 > :nth-child(-n+3), .stars--4 > :nth-child(-n+4), .stars--5 > :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  justify-content: space-between;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  align-self: center;
  height: 40px;
}

.header__nav {
  align-self: center;
}

.header__nav-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.header__nav-item {
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  margin: 0 10px;
  display: flex;
}

.header__nav-item:last-child {
  margin-right: 0;
}

.header__nav-item:first-child {
  margin-left: 0;
}

.header__nav-link {
  color: #060b35;
  align-items: center;
  height: 60px;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
}

.header__nav-link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  border-bottom: 4px solid #00acdc;
  border-radius: 8px;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
}

.catalog {
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 1px) {
  .catalog {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
